@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap);



header{padding:40px 0 156px}.theme-light header{background-color:#f7f9ff}.theme-dark header{background-color:#1F212E}header h1{margin:0;font-weight:700}.theme-light header h1{color:#1E202A}.theme-dark header h1{color:#fff}header p{margin:0;font-weight:700}header .container{display:grid;justify-content:space-between;grid-template-columns:1fr auto;align-items:center}@media (max-width: 768px){header .container{grid-template-columns:1fr;grid-row-gap:1rem;row-gap:1rem}header h1{font-size:1.5em}header .title::after{content:'';display:inline-block;width:100%;height:1px}.theme-light header .title::after{background-color:#63687E}.theme-dark header .title::after{background-color:#8B97C6}}

.container-switch .text{vertical-align:middle;margin-right:15px;font-weight:700;text-transform:capitalize}.switch{position:relative;display:inline-block;width:50px;height:24px}.switch input{opacity:0;width:0;height:0}.slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background:linear-gradient(90deg, #378FE6 10%, #3EDA82 100%);transition:.4s}.slider:before{position:absolute;content:"";height:16px;width:16px;left:4px;bottom:4px;background-color:#1F212E;transition:.4s}input:checked+.slider{background:#AEB2CE}input:checked+.slider:before{background:#fff}input:focus+.slider{box-shadow:0 0 1px #2196F3}input:checked+.slider:before{-webkit-transform:translateX(27px);transform:translateX(27px)}.slider.round{border-radius:34px}.slider.round:before{border-radius:50%}@media (max-width: 768px){.container-switch{display:grid;grid-template-columns:1fr 1fr}.switch{justify-self:end}}

.card-total{text-align:center;padding:1.5rem 0;overflow:hidden}.card-total:before{content:'';position:absolute;left:0;right:0;top:0;height:4px}.card-total .link::before{vertical-align:middle;margin-right:0.5rem}.card-total.facebook::before{background-color:#1CA0F2}.card-total.facebook .link::before{content:url(/static/media/icon-facebook.a7383764.svg)}.card-total.twitter::before{background-color:#00bfff}.card-total.twitter .link::before{content:url(/static/media/icon-twitter.810ccee8.svg)}.card-total.instagram::before{background:#FDC468;background:linear-gradient(90deg, #FDC468 0%, #DF4996 100%)}.card-total.instagram .link::before{content:url(/static/media/icon-instagram.991487fe.svg)}.card-total.youtube:before{background-color:#C4032A}.card-total.youtube .link::before{content:url(/static/media/icon-youtube.8499da12.svg)}.card-total .followers{font-size:3.5em;font-weight:700;line-height:1.2;margin-top:1rem}.theme-light .card-total .followers{color:#1E202A}.theme-dark .card-total .followers{color:#fff}.card-total .caption{letter-spacing:4px;text-transform:uppercase;font-size:0.875em}.card-total .bounce-rate{margin-top:1.5rem}

.cardDaily{display:grid;justify-content:center;align-items:center;grid-template-columns:1fr 1fr;padding:1.5rem 1rem;grid-template-areas:"title title" "views bounceRate"}.cardDaily.facebook{order:1}.cardDaily.instagram{order:2}.cardDaily.twitter{order:3}.cardDaily.youtube{order:4}.cardDaily .title{grid-area:title;font-weight:700;position:relative;margin-bottom:1rem}.cardDaily .title::after{display:inline-block;position:absolute;right:0;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.cardDaily .title.facebook::after{content:url(/static/media/icon-facebook.a7383764.svg)}.cardDaily .title.twitter::after{content:url(/static/media/icon-twitter.810ccee8.svg)}.cardDaily .title.instagram::after{content:url(/static/media/icon-instagram.991487fe.svg)}.cardDaily .title.youtube::after{content:url(/static/media/icon-youtube.8499da12.svg)}.cardDaily .views{grid-area:views;font-size:2em;align-self:end;line-height:1}.cardDaily .bounce-rate{grid-area:bounceRate;text-align:right;align-self:end;line-height:1}

body{font-size:14px;font-family:'Inter', sans-serif;font-weight:400}.App{min-height:100vh}a{text-decoration:none}.theme-light a{color:#63687E}.theme-dark a{color:#8B97C6}.container{width:1110px;margin:0 auto}.theme-light .container{color:#63687E}.theme-dark .container{color:#8B97C6}.theme-light{background-color:#fff}.theme-dark{background-color:#1E202A}main{margin-top:-100px}p{margin:0}section{padding:0 0 1.5rem}.card-list{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-column-gap:30px;-webkit-column-gap:30px;column-gap:30px;grid-row-gap:20px;row-gap:20px;align-items:center}.card-list .card{border-radius:0.5em;position:relative;cursor:pointer}.theme-light .card-list .card{background-color:#F0F2FA}.theme-dark .card-list .card{background-color:#252A41}.theme-light .card-list .card{color:#63687E}.theme-dark .card-list .card{color:#8B97C6}.theme-light .card-list .card:hover{background-color:#E1E3F0}.theme-dark .card-list .card:hover{background-color:#333A56}.card-list .card .link::after{content:'';display:inline-block;position:absolute;left:0;top:0;right:0;bottom:0;z-index:1}.card-list .bounce-rate{font-size:0.75rem;font-weight:700}.card-list .bounce-rate::before{content:'';display:inline-block;width:0;height:0;border-style:solid;border-width:0 4px 5px 4px;margin-right:0.2rem}.card-list .bounce-rate.positive:before{border-color:transparent transparent #1DB489 transparent}.card-list .bounce-rate.negative:before{border-color:transparent transparent #DC414C transparent;-webkit-transform:rotate(180deg);transform:rotate(180deg)}.green{color:#1DB489}.red{color:#DC414C}@media (max-width: 992px){.card-list{grid-template-columns:1fr 1fr}}@media (max-width: 768px){.card-list{grid-template-columns:1fr}}@media (max-width: 1200px){.container{width:100%;padding:0 2rem}}

